<template>
  <div class="paymentsettings">
    <hx-Return :title="title"></hx-Return>
    <div class="title" v-show="isshow">修改密码</div>
    <div class="title" v-show="!isshow">设置密码</div>
    <van-password-input :value="value" :mask="false" :focused="showKeyboard" />
    <van-number-keyboard :show="showKeyboard" @input="onInput" @delete="onDelete" />
  </div>
</template>

<script>
import hxReturn from "../../components/hxReturn";

export default {
  data() {
    return {
      title: "支付设置",
      value: "",
      showKeyboard: true,
      isshow:false,
    };
  },
  methods: {
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);
      if(this.value.length == 6){
          this.$store.state.Paymentpassword = this.value
          this.$toast("设置密码成功");
          this.$router.back(-1)
      }
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    }
  },
  created() {
    this.$store.state.vantTab = false;
    if(this.$store.state.Paymentpassword != "" ){
        this.isshow = true;
    }else{
        this.isshow = false;
    }

  },

  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.paymentsettings {
  padding-top: 50px;
  .title{
      text-align: center;
      height: 70px;
      line-height: 70px;
  }
}
</style>